function getPrompts() {
    return JSON.parse(localStorage.getItem('prompts') || '[]');
}

function savePrompts(prompts) {
    localStorage.setItem('prompts', JSON.stringify(prompts));
}

function getLastSelectedPromptIndex() {
    return parseInt(localStorage.getItem('lastSelectedPromptIndex') || '-1', 10);
}

function saveLastSelectedPromptIndex(index) {
    localStorage.setItem('lastSelectedPromptIndex', index.toString());
}



function initSettings() {
    const settingsTab = document.getElementById('settings');
    settingsTab.innerHTML = `
        <div class="mt-3">
            <label for="api-key" class="form-label">API Ключ:</label>
            <input type="password" id="api-key" class="form-control">
        </div>
    `;

    document.getElementById('api-key').onchange = saveApiKey;
    loadModels();
    loadSettings();
}

function populateModelSelectM(models) {
    const select = document.getElementById('prompt-model');
    select.innerHTML = '';
    models.forEach(model => {
        const option = document.createElement('option');
        option.value = model.id;
        option.textContent = model.name;
        select.appendChild(option);
    });
}

function saveApiKey() {
    const apiKey = document.getElementById('api-key').value;
    localStorage.setItem('apiKey', apiKey);
}

function loadSettings() {
    const apiKey = localStorage.getItem('apiKey') || '';
    document.getElementById('api-key').value = apiKey;
}




const OPENROUTER_BASE_URL = "https://openrouter.ai/api/v1";

async function loadModels() {
    try {
        const response = await axios.get(`${OPENROUTER_BASE_URL}/models`);
        const models = response.data.data;
        localStorage.setItem('models', JSON.stringify(models));
        populateModelSelectM(models);
    } catch (error) {
        console.error('Error loading models:', error);
    }
}

async function generateTextWithAI(prompt, model, signal) {
    const apiKey = localStorage.getItem('apiKey');

    try {
        const response = await fetch(`${OPENROUTER_BASE_URL}/chat/completions`, {
            method: 'POST',
            signal,
            headers: {
                'Authorization': `Bearer ${apiKey}`,
                'Content-Type': 'application/json',
                'HTTP-Referer': 'https://www.office.com',
                'X-Title': 'Word AI Assistant',
            },
            body: JSON.stringify({
                model: model,
                messages: [{ role: "user", content: prompt }],
            }),
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        return data.choices[0].message.content.trim();
    } catch (error) {
        console.error('Error in generateTextWithAI:', error);
        throw error;
    }
}




let originalText = "";
let currentController = null;
let lastSelectedPrompt = null;

console.log("Привет мир")

function initHome() {
    const homeTab = document.getElementById('home');
    homeTab.innerHTML = `
        <select id="prompt-select" class="form-select mt-3"></select>
        <div id="selected-model" class="mt-2"></div>
        <button id="replace-text" class="btn btn-primary mt-3" disabled>Заменить выделенный текст</button>
        <button id="cancel-request" class="btn btn-secondary mt-3" style="display: none;">Отменить запрос</button>
    `;

    $('#prompt-select').select2({
        width: '100%',
        placeholder: 'Выберите промпт',
        templateResult: formatPrompt,
        templateSelection: formatPromptSelection,
    });

    document.getElementById("replace-text").onclick = replaceSelectedText;
    document.getElementById("cancel-request").onclick = cancelRequest;

    updatePromptSelect();
    setInterval(checkSelection, 1000);
}

function formatPrompt(prompt) {
    if (!prompt.id) {
        return prompt.text;
    }
    const prompts = getPrompts();
    const promptData = prompts[prompt.id];
    return $(`<span>${promptData.name} (${promptData.model})</span>`);
}

function formatPromptSelection(prompt) {
    if (!prompt.id) {
        return prompt.text;
    }
    const prompts = getPrompts();
    const promptData = prompts[prompt.id];
    return promptData.name;
}

function updatePromptSelect() {
    const select = $('#prompt-select');
    const modelDiv = document.getElementById('selected-model');
    const prompts = getPrompts();
    select.empty();
    select.append(new Option('Выберите промпт', '', true, true));
    const lastSelectedPromptIndex = getLastSelectedPromptIndex();
    prompts.forEach((prompt, index) => {
        const option = new Option(prompt.name, index, false, index === lastSelectedPromptIndex);
        select.append(option);
    });
    select.trigger('change');
    const promptIndex = lastSelectedPromptIndex >= 0 && lastSelectedPromptIndex < prompts.length ? lastSelectedPromptIndex : null;
    lastSelectedPrompt = promptIndex !== null ? prompts[promptIndex] : null;
    modelDiv.textContent = lastSelectedPrompt ? `Модель: ${lastSelectedPrompt.model}` : '';
    document.getElementById("replace-text").disabled = !lastSelectedPrompt;
    select.on('change', function() {
        const newPromptIndex = $(this).val();
        lastSelectedPrompt = newPromptIndex ? prompts[newPromptIndex] : null;
        saveLastSelectedPromptIndex(newPromptIndex || -1);
        modelDiv.textContent = lastSelectedPrompt ? `Модель: ${lastSelectedPrompt.model}` : '';
        document.getElementById("replace-text").disabled = !lastSelectedPrompt;
    });
}

function checkSelection() {
    Word.run(async (context) => {
        const selection = context.document.getSelection();
        selection.load("text");
        await context.sync();

        const replaceButton = document.getElementById("replace-text");
        if (selection.text.trim().length > 0) {
            replaceButton.disabled = false;
        } else {
            replaceButton.disabled = true;
        }
    });
}

async function replaceSelectedText() {
    Word.run(async (context) => {
        const selection = context.document.getSelection();
        selection.load("text");
        await context.sync();

        originalText = selection.text;

        if (!lastSelectedPrompt) return;

        document.getElementById("replace-text").style.display = "none";
        document.getElementById("cancel-request").style.display = "inline-block";

        try {
            currentController = new AbortController();
            const fullPrompt = `${lastSelectedPrompt.text}\n\nUser provided the following text: "${originalText}"`;
            const generatedText = await generateTextWithAI(fullPrompt, lastSelectedPrompt.model, currentController.signal);
            selection.insertText(generatedText + '\n', Word.InsertLocation.replace);
        } catch (error) {
            if (error.name !== 'AbortError') {
                console.error('Error:', error);
            }
        } finally {
            document.getElementById("cancel-request").style.display = "none";
            document.getElementById("replace-text").style.display = "inline-block";
            currentController = null;
        }
    });
}

function cancelRequest() {
    if (currentController) {
        currentController.abort();
        document.getElementById("cancel-request").style.display = "none";
        document.getElementById("replace-text").style.display = "inline-block";
    }
}




function initPrompts() {
    const promptsTab = document.getElementById('prompts');
    promptsTab.innerHTML = `
        <div class="mt-3">
            <input type="text" id="prompt-name" class="form-control" placeholder="Название промпта">
            <select id="prompt-model" class="form-select mt-2"></select>
            <textarea id="prompt-text" class="form-control mt-2" rows="5" placeholder="Текст промпта"></textarea>
            <button id="save-prompt" class="btn btn-primary mt-2">Сохранить промпт</button>
        </div>
        <div id="prompt-list" class="mt-3"></div>
    `;

    $('#prompt-model').select2({
        width: '100%',
        placeholder: 'Выберите модель',
    }).on('select2:opening', function() {
        setTimeout(() => {
            $('.select2-search__field').get(0).focus();
        }, 0);
    });

    document.getElementById("save-prompt").onclick = savePrompt;
    updatePromptList();
    populateModelSelect();
}

function populateModelSelect() {
    const select = $('#prompt-model');
    const models = JSON.parse(localStorage.getItem('models') || '[]');
    models.forEach(model => {
        const option = new Option(model.name, model.id, false, false);
        select.append(option);
    });
    select.trigger('change');
}

function savePrompt() {
    const name = document.getElementById('prompt-name').value;
    const model = $('#prompt-model').val();
    const text = document.getElementById('prompt-text').value;
    if (name && model && text) {
        const prompts = getPrompts();
        prompts.push({ name, model, text });
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        document.getElementById('prompt-name').value = '';
        $('#prompt-model').val(null).trigger('change');
        document.getElementById('prompt-text').value = '';
    }
}

function updatePromptList() {
    const list = document.getElementById('prompt-list');
    const prompts = getPrompts();
    list.innerHTML = '';
    prompts.forEach((prompt, index) => {
        const div = document.createElement('div');
        div.className = 'mt-2';
        div.innerHTML = `
            <strong>${prompt.name}</strong> (${prompt.model})
            <button class="btn btn-sm btn-primary edit-prompt" data-index="${index}">Редактировать</button>
            <button class="btn btn-sm btn-info duplicate-prompt" data-index="${index}">Дубликат</button>
            <button class="btn btn-sm btn-danger delete-prompt" data-index="${index}">Удалить</button>
        `;
        list.appendChild(div);
    });

    list.querySelectorAll('.delete-prompt').forEach(button => {
        button.onclick = () => deletePrompt(button.dataset.index);
    });

    list.querySelectorAll('.edit-prompt').forEach(button => {
        button.onclick = () => editPrompt(button.dataset.index);
    });

    list.querySelectorAll('.duplicate-prompt').forEach(button => {
        button.onclick = () => duplicatePrompt(button.dataset.index);
    });
}

function deletePrompt(index) {
    const prompts = getPrompts();
    prompts.splice(index, 1);
    savePrompts(prompts);
    updatePromptList();
    updatePromptSelect();
}

function editPrompt(index) {
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('prompt-name').value = prompt.name;
    $('#prompt-model').val(prompt.model).trigger('change');
    document.getElementById('prompt-text').value = prompt.text;
    document.getElementById('save-prompt').textContent = 'Обновить промпт';
    document.getElementById('save-prompt').onclick = () => {
        updatePrompt(index);
        document.getElementById('save-prompt').textContent = 'Сохранить промпт';
        document.getElementById('save-prompt').onclick = savePrompt;
    };
}

function updatePrompt(index) {
    const name = document.getElementById('prompt-name').value;
    const model = $('#prompt-model').val();
    const text = document.getElementById('prompt-text').value;
    if (name && model && text) {
        const prompts = getPrompts();
        prompts[index] = { name, model, text };
        savePrompts(prompts);
        updatePromptList();
        updatePromptSelect();
        document.getElementById('prompt-name').value = '';
        $('#prompt-model').val(null).trigger('change');
        document.getElementById('prompt-text').value = '';
    }
}

function duplicatePrompt(index) {
    const prompts = getPrompts();
    const prompt = prompts[index];
    document.getElementById('prompt-name').value = prompt.name + ' (копия)';
    $('#prompt-model').val(null).trigger('change');
    document.getElementById('prompt-text').value = prompt.text;
    document.getElementById('save-prompt').textContent = 'Сохранить промпт';
    document.getElementById('save-prompt').onclick = savePrompt;
}




// 

Office.onReady((info) => {
    if (info.host === Office.HostType.Word) {
        initHome();
        initPrompts();
        initSettings();
    }
});


